import Banner from "./Banner";
import NavBar from "./NavBar";
import {useState} from 'react';
import "../css/card.css";

const Home = () => {
    const [headings, setHeadings] = useState(['About']);
    return (
        <div className="container-fluid bg-dark" style={{backgroundColor: "#272727", height: "100vh"}}>
            <NavBar title='Sibtain Hussain' headings={headings} theme='dark'/>
            <div className="container-fluid row bg-dark" style={{alignItems: "center"}}>
                <div className="col h-100"><Banner /></div>
                
                <div className="col-3 collapse collapse-horizontal show align-self-center" id="aboutSidebar">
                    <div className="card card-body h-100" id="c0">
                        <p></p>
                        <p className="card-title">Undergraduate Sophomore Student</p>
                        <p className="card-title">@ Virginia Tech</p>
                        <p></p>
                        <p>BS in Computer Science</p>
                        <p>Minor in Japanese Studies</p>
                        <p></p>
                        <p>sibtainhussain101@gmail.com</p>
                        <p>(703) 708-4292</p>
                    </div>
                </div>
            </div>

            <div className="card-group h-100">

                <div id="c1" className="card bg-dark">
                    <div className="card-body">
                        <h2 className="card-title">Awards</h2>
                        <p className="card-text">This is a wider card with supporting text below as a natural lead-in to additional content. This content is a little bit longer.</p>
                    </div>
                </div>

                <div id="c2" className="card bg-dark">
                    <div className="card-body">
                        <h2 className="card-title">Education</h2>
                        <p className="card-text">This is a wider card with supporting text below as a natural lead-in to additional content. This content is a little bit longer.</p>
                    </div>
                </div>

                <div id="c3" className="card bg-dark">
                    <div className="card-body">
                        <h2 className="card-title">Skills</h2>
                        <p className="card-text">This is a wider card with supporting text below as a natural lead-in to additional content. This content is a little bit longer.</p>
                    </div>
                </div> 

                <div id="c4" className="card bg-dark">
                    <div className="card-body">
                        <h2 className="card-title">Activities</h2>
                        <p className="card-text">This is a wider card with supporting text below as a natural lead-in to additional content. This content is a little bit longer.</p>
                    </div>
                </div> 

            </div>
        </div>
    )
}

export default Home
