const Banner = () => {
    return (
        <div className="container-fluid" style={{color: "#66FCF1", height: "300px"}}>
            
            <div className="row h-100">
                
                <div className="col align-self-start h-25" style={{textAlign:'right', position: "relative"}}>
                    <h1 style={{fontFamily: "courier new", fontSize: "30px", position: "absolute", bottom: "0", right: "0"}}>I am</h1>
                </div>
                
                <div className="col align-self-center h-100" style={{display: 'flex',  textAlign:'center', alignItems:'center'}}>
                    <h1 style={{fontFamily: "courier new", fontSize: "80px"}}>SIBTAIN HUSSAIN</h1>
                </div>
                
                <div className="col align-self-end h-25" style={{textAlign:'left', alignItems:'start'}}>
                    <h1 style={{fontFamily: "courier new", fontSize: "30px"}}>Welcome</h1>
                </div>

            </div>

        </div>
    )
}

export default Banner
