const NavBar = ({title, headings, theme}) => {
    return (
        <nav className={`navbar navbar-expand-lg navbar-${theme} bg-${theme}`} style={{backgroundColor: "#EBEBEB"}}>
            <div className='container-fluid'>
                <a className='navbar-brand' href='#'>
                    {title}
                </a>
                <ul className='navbar-nav ms-auto'>
                    {headings.map((page) => {
                        if (Array.isArray(page)){
                            return <li className="nav-item dropdown">
                                <a className="nav-link dropdown-toggle" href='#' id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                    {page[0]}
                                </a>
                                <ul className={`dropdown-menu dropdown-menu-${theme} dropdown-menu-end`} aria-labelledby="navbarDropdown">
                                    {page.filter((item, index) => index > 0).map((subPage => (
                                        <li><a className="dropdown-item" href={subPage}>{subPage}</a></li>
                                    )))}
                                </ul>
                            </li>
                        }
                        else if (typeof page === 'string'){
                            if(page === 'About'){
                                return <li className='nav-item'><button className="btn btn-outline-info" type="button" data-bs-toggle="collapse" data-bs-target="#aboutSidebar" aria-expanded="true" aria-controls="aboutSidebar">{page}</button></li>
                            }
                            else{
                                return <li className='nav-item'><a className='nav-link' href={page}>{page}</a></li>
                            }
                        }
                        else{
                            return page
                        }
                    })
                    }
                </ul>
            </div>
        </nav>
    );
};

export default NavBar;
